import React from 'react';
import logo from "./logo2.png";

const Header = () =>{
    return(
        <>
            <div className="header">
                <img src={logo} alt="logo" width="70" height="70"/>
                <h1>Google <span className="keep">Keep</span></h1>
            </div>
        </>
    ); 
}

export default Header;